	import interfaces from '../../utils/interfaces.js';
import menus from "../../components/menu.vue"
import {
        mapState,
        mapMutations
    } from 'vuex';

export default {
		computed: {
		            ...mapState(['type'])
		        },
	components:{
		menus
		
	},
	data() {
		return {
			pageNum:1,
			hasNext:true,
			cardCur: 0,
			swiperList: [
			],
			video:'',
			goodsList:[],
			newList:[],
			latitude:"",
			longitude:"",
		}
	},
	onLoad() {
		
		
		/* wx.getLocation({
		type: 'gcj02',
		success: (res) => {
		console.log(res)
		
		},
		}) */

	
	/* 	uni.chooseLocation({
			latitude: 108.962269,//要去的纬度-地址
			longitude: 34.171002,//要去的经度-地址
		    success: function (ress) {
		        console.log('位置名称：' + ress.name);
		        console.log('详细地址：' + ress.address);
		        console.log('纬度：' + ress.latitude);
		        console.log('经度：' + ress.longitude);
		    }
		}); */
		
			this.getData();
			this.getProduct();
			
			this.getNews();
	},
	watch:{
		type(){
			
			this.getProduct();
			this.hasNext=true;
			this.pageNum=1;
			this.getNews();
		}
	},
	methods: {
		...mapMutations(['changeType']),
		
		getData(){
			this.request({
				url: interfaces.getLunbo,
				data: {
				       
				    },
				success: ((res) => {
					if(res.errno==0){
						this.swiperList=res.data;
					}
					console.log(res);
				
			
				})
			});
			this.request({
				url: interfaces.getvideo,
				success: ((res) => {
					if(res.errno==0){
						if(res.data!=null){
						this.video=res.data.gUrl;	
						}
						
						
						/* for(let i=0;i<this.swiperList.length;i++){
							if(this.swiperList[i].gType=="video"){
								this.video=this.swiperList[i];
							}
							
						
						} */
					}
					
				
			
				})
			});
		},
		onPullDownRefresh() {
		        //监听下拉刷新动作的执行方法，每次手动下拉刷新都会执行一次
				this.hasNext=true;
				this.pageNum=1;
				this.getData();
				this.getProduct();
				
				this.getNews();
				
				 uni.stopPullDownRefresh();
		    },
		onReachBottom(){
				if(this.hasNext==true){
					this.request({
						url: interfaces.getNewsLimit,
						data: {
						       type:this.type,
							   pageNum:this.pageNum,
							   pageSize:4
						    },
						success: ((res) => {
							console.log(res)
							if(res.errno==0){
								this.pageNum=res.data.pageNum+1;
								this.hasNext=res.data.hasNextPage;
								for(let i=0;i<res.data.list.length;i++){
									
									this.newList.push(res.data.list[i]);
								
								}
								
							}
							
						
					
						})
					})
				}else{
					if(this.type=="c"){
						setTimeout(function(){
						         uni.showToast({
						                        icon:"none",
						         				duration:2000,
						                        title:"没有更多了"
						                    });
						          }, 500)
					}else{
						setTimeout(function(){
						         uni.showToast({
						                        icon:"none",
						         				duration:2000,
						                        title:"No more"
						                    });
						          }, 500)
						
					}
				}
		},
		getProduct(){
			
			
			
			this.request({
				url: interfaces.getGoodsLimit,
				data: {
				       type:this.type
				    },
				success: ((res) => {
					if(res.errno==0){
						this.goodsList=res.data;
					}
					
				
			
				})
			})
		},
		getNews(){
			this.newList=[];
			this.pageNum=1;
			
			
			if(this.hasNext==true){
				this.request({
					url: interfaces.getNewsLimit,
					data: {
					       type:this.type,
						   pageNum:this.pageNum,
						   pageSize:4
					    },
					success: ((res) => {
						this.pageNum++;
						console.log(res)
						if(res.errno==0){
							
							this.hasNext=res.data.hasNextPage;
							this.newList=res.data.list;
							
						}
						
					
				
					})
				})
			}
		
		},
		intonews(id){
			uni.navigateTo({
			                    url:"../news/news?id="+id,
			                    success: res => {},
			                    fail: () => {},
			                    complete: () => {}
			                });
			
		},
		intoproduct(id){
			uni.navigateTo({
				url:"../product/product?id="+id,
				success: res => {},
				fail: () => {},
				complete: () => {}
			})
		}
	}
}
